import { Container, Row, Col } from 'react-bootstrap';
import { Component } from 'react';
import introText from './texts/introductionText';
import uility from './texts/utilityText';
import gen2t from './texts/gen2t';
import gen2u from './texts/gen2u';
import lp from './texts/lp';


class ProjectIntro extends Component{

    render(){
        return(
            <div id="introduction" className='introContainer'>   
                <h2>Introduction</h2>         
                <Container>
                    <Row>
                        <Col className='sneakpeeksCol'>
                        <Container className="mediaContainer NFT_Carousel">
                        
                        </Container>
                        </Col>
                        <Col className='sneakpeeksCol'>
                        <Container className="mediaContainer NFT_Carousel">
                        
                        </Container>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Container>
                                <h3>Gen1 Tokenomics</h3>
                                <p>
                                 {introText}
                                </p>
                            </Container>
                        </Col>
                        <Col>
                            <Container>
                                <h3>Gen1 Utility</h3>
                                <p>
                                 {uility}
                                </p>
                            </Container>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Container>
                                    <h3>Gen2 Tokenomics </h3>
                                    <p>
                                    {gen2t}
                                    </p>
                                </Container> 
                        </Col>
                        <Col>
                            <Container>
                                    <h3>Gen2 Utility</h3>
                                    <p>
                                    {gen2u}
                                    </p>
                                </Container> 
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Container>
                                    <h3>Gen2 Liquidity Pool </h3>
                                    <p>
                                    {lp}
                                    </p>
                                </Container> 
                        </Col>
                    </Row>
                </Container> 
            </div>
        )
    }
}

export default (ProjectIntro);